@use '../../../assets/styles/variables.scss' as v;

.list {
  list-style-type: none;
  padding: 0;
  font: 600 18px/25px 'Open Sans';

  &__item:first-child {
    font-weight: 400;
  }

  &__item:not(:last-child) {
    margin-bottom: 16px;
  }

  img {
    scale:1.6;
  }
}

@media (max-width:1090px) {
  .list {
    img {
      scale: 1
    }
  }
}

@media (max-width:1090px) {
  .list {
    text-align: center;
  }
}
